<template>
  <div class="hello">
    <el-checkbox v-model="checked" @change="handleCheckBox">关联子节点</el-checkbox>
    <el-tree :data="data" show-checkbox node-key="id" :default-expanded-keys="[2, 3]" :default-checked-keys="defaultChecked" :props="defaultProps">
    </el-tree>
  </div>
</template>

<script>
export default {
  data () {
    return {
      checked: false,
      data: [{
        id: 1,
        label: '一级 1',
        children: [{
          id: 4,
          label: '二级 1-1',
          children: [{
            id: 9,
            label: '三级 1-1-1'
          }, {
            id: 10,
            label: '三级 1-1-2'
          }]
        }]
      }, {
        id: 2,
        label: '一级 2',
        children: [{
          id: 5,
          label: '二级 2-1'
        }, {
          id: 6,
          label: '二级 2-2'
        }]
      }, {
        id: 3,
        label: '一级 3',
        children: [{
          id: 7,
          label: '二级 3-1'
        }, {
          id: 8,
          label: '二级 3-2'
        }]
      }],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      defaultChecked: [1, 2, 3]
    };
  },
  created () {
    this.getData()
  },
  methods: {
    getData () {
      // this.$http({
      //   url: 'support/system/sysuser/getUserInfo',
      //   method: 'post',
      //   params: {}
      // }).then(res => {
      //   console.log(res)
      // })
    },
    handleCheckBox (val) {
      let defaultChecked = []
      if (val) {
        defaultChecked.push(1)
      }
      this.defaultChecked = JSON.parse(JSON.stringify(defaultChecked))
    },
    handleNodeClick (data) {
      console.log(data);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



