import Vue from 'vue'
import axios from 'axios'
import router from '@/router'
import qs from 'qs'
import merge from 'lodash/merge'
import {
  Message
} from 'element-ui'
// import {
//   clearLoginInfo
// } from '@/utils'
// import {
//   baseRouter
// } from '../main.js'
const http = axios.create({
  timeout: 10000 * 10,
  // withCredentials: true,
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8'
  }
})

/**
 * 请求拦截
 */
http.interceptors.request.use(config => {
  let token = sessionStorage.getItem('token');
  if (token) {
    config.headers['Token'] = JSON.parse(token) // 请求头带上token
  }
  return config
}, error => {
  return Promise.reject(error)
})

/**
 * 响应拦截
 */

http.interceptors.response.use(response => {

  if (response.data && (response.data.code == 10100 || response.data.code == 10101)) { // 10100, token失效; 10101,被踢出

    if (window.__POWERED_BY_QIANKUN__) {
      // baseRouter().push({
      //   name: 'login'
      // })
      window.location.reload()
      // 加这个是因为过期后在进入会直接进入/main，不会出现菜单栏，主应用的router.js只会刚开始的时候加载一次，还因为虽然这一个请求拦截了，跳转到了主应用的login中去了，但是后边还有好多请求可能，就走else中去了，就跳转到子应用中的login中去了/gbxx/login中
    } else {
      // clearLoginInfo()
      window.sessionStorage.clear()
      router.push({
        name: 'login'
      })
    }
  }
  return response
}, error => {
  Message({
    type: 'error',
    message: '请求异常，请检查网络！'
  });
  return Promise.reject(error)
})

/**
 * 请求地址处理
 * @param {*} actionName action方法名称
 */
http.adornUrl = (actionName) => {
  // 非生产环境 && 开启代理, 接口前缀统一使用[/proxyApi/]前缀做代理拦截!
  /** @namespace process.env.OPEN_PROXY */
  return (process.env.NODE_ENV !== 'production' && process.env.OPEN_PROXY ? '/api/' : window.SITE_CONFIG.baseUrl) + actionName
}

/**
 * get请求参数处理
 * @param {*} params 参数对象
 * @param {*} openDefultParams 是否开启默认参数?
 */
http.adornParams = (params = {}, openDefultParams = true) => {
  var defaults = {
    // 't': new Date().getTime()
  }
  return openDefultParams ? merge(defaults, params) : params
}

/**
 * post请求数据处理
 * @param {*} data 数据对象
 * @param {*} openDefultdata 是否开启默认数据?
 * @param {*} contentType 数据格式
 *  json: 'application/json; charset=utf-8'
 *  form: 'application/x-www-form-urlencoded; charset=utf-8'
 */
http.adornData = (data = {}, openDefultdata = true, contentType = 'form') => {
  var defaults = {
    // 't': new Date().getTime()
  }
  data = openDefultdata ? merge(defaults, data) : data
  return contentType === 'json' ? JSON.stringify(data) : qs.stringify(data)
}


export default http



